<template>
    <div>
        <div class="home">
            <div class="container">
                <div class="row">
                    <div class="col-lg-10 offset-lg-1">
                        <div class="home-title">
                            <span>图片</span>
                        </div>
                    </div>
                    <div class="col-lg-8 offset-lg-2">
                        <div class="home-content">
                            <div class="row">
                                <div class="col-lg-6 mb-3" v-for="(image, index) of imgData" :key="index">
                                    <img :src="image.url" alt="">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-10 offset-lg-1">
                        <div class="home-title">
                            <span>博客</span>
                        </div>
                    </div>
                    <div class="col-lg-8 offset-lg-2">
                        <div class="home-content">
                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="home-blog">
                                        <h4><a href="#">博客标题</a></h4>
                                        <p>博客描述</p>
                                        <span>创建日期：</span>
                                    </div>
                                    <div class="home-blog">
                                        <h4><a href="#">博客标题</a></h4>
                                        <p>博客描述</p>
                                        <span>创建日期：</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            imgData: []
        }
    },
    mounted() {
        this.$axios.get("/static/mock/index.json").then(res => {
            this.imgData = res.data.work.image.slice(0, 4)
            console.log(this.imgData);
        })
    },
    methods: {
        
    }
}
</script>

<style scoped>
    .home {
        margin: 2rem 0;
    }
    .home-title, .home-content {
        margin-bottom: 2rem;
    }
    .home-title span {
        font-weight: bold;
        display: block;
        width: fit-content;
        margin: 0 auto;
        padding: 8px 40px;
        background: #000;
        color: #fff;
    }
    .home-image {
        margin-bottom: 1rem;
    }
    .home-image:last-of-type {
        margin-bottom: 0;
    }
    img {
        display: block;
        max-width: 100%;
        max-height: 100%;
    }
    .home-blog {
        background: #f1f1f1;
        padding: 1rem;
        margin-bottom: 1rem;
    }
    .home-blog:last-of-type {
        margin-bottom: 0;
    }
    .home-blog h4 a {
        color: #000;
        text-decoration: underline;
    }
    .home-blog h4 a:hover {
        text-decoration: underline dotted #000;
    }
    .home-blog p {
        margin: 0;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
    }
    .home-blog span {
        display: block;
        text-align: right;
        font-size: 0.8rem;
    }
</style>


